import React from "react";
import "./styles.css";

const BASE_URL = "https://wa.me/";

export default function App() {
  const [initialValue, setValue] = React.useState({
    phoneNumber: 1234567890,
    textMessage: "Hello, how are you?",
    finalUrl: ""
  });

  function onChangeEvent(event: any) {
    event.preventDefault();

    const data = Object.assign(initialValue, {
      [event.target.name]: event.target.value
    });

    const text = encodeURIComponent(data.textMessage);
    const finalUrl = `${BASE_URL}${data.phoneNumber}?text=${text}`;

    setValue({
      ...initialValue,
      ...data,
      finalUrl
    });
  }

  function copyLink() {
    if (initialValue.finalUrl) {
      navigator.clipboard.writeText(initialValue.finalUrl);
      alert("Copied the text: " + initialValue.finalUrl);
    }
  }

  return (
    <div className="App">
      <div className="container">
        <div className="whatsapp-img">
          <img
            src="data:image/png;base64,"
            alt="whatsapp"
          />
        </div>
        <div className="form">
          <div className="group">
            <label>Phone Number: </label>
            <input
              type="text"
              id="phoneNumber"
              name="phoneNumber"
              value={initialValue.phoneNumber}
              onChange={(event) => {
                onChangeEvent(event);
              }}
            />
          </div>
          <div className="group">
            <label>Message: </label>
            <input
              type="text"
              name="text"
              value={initialValue.textMessage}
              onChange={(event) => {
                onChangeEvent(event);
              }}
            />
          </div>
          <div className="group">
            <label>URL: </label>
            <input
              type="text"
              name="finalUrl"
              readOnly
              value={initialValue.finalUrl}
            />
          </div>
          <div className="button-group">
            <button
              type="button"
              name="copyLink"
              onClick={() => {
                copyLink();
              }}
            >
              Copy Link
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
